import React, {Component} from 'react';
import axios from 'axios';

class ProfileDetails extends Component {
  constructor(props) {
    super(props);

    this.state = {
      profile: {},
    };
  }

  componentDidMount() {
    let p = window.location.pathname.split('/');
    console.log(p);

    let out;

    axios.get('/api/v1/profile/')
    .then(res => {
      for (let i=0; i<res.data.length; i++) {
        if (res.data[i].id === parseInt(p[3])) {
          this.setState({profile: res.data[i]});
        }
      }
      console.log(out);
    })
    .catch(error =>{
      console.log(error);
    });
  }

  render(){
    return (
      <section className='aside-content'>
        <h1>{this.state.profile.name}</h1>
        <img src={this.state.profile.image} alt='' />
        <p>{this.state.profile.preference1}</p>
        <p>{this.state.profile.preference2}</p>
        <p>{this.state.profile.preference3}</p>
      </section>
    );
  };
}

export default ProfileDetails;
