import React, {Component} from 'react';
import axios from 'axios';


axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = 'X-CSRFToken';

class ProfileCreate extends Component {

  constructor(props) {
    super(props);

    this.state = {
      name: '',
      avatar: null,
      preview: '',
      preference1: false,
      preference2: false,
      preference3: false,
      profile: [],
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleAvatarChange = this.handleAvatarChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  handleChange(e) {
    console.log(e.target.checked)
    this.setState({[e.target.name]: e.target.value});

  }

  handleAvatarChange(e) {
    let file = e.target.files[0];
    this.setState({avatar: file});

    let reader = new FileReader();
    reader.onloadend = () => {
      this.setState({preview: reader.result});
    };
    reader.readAsDataURL(file);
  }

  handleSubmit(e) {
    e.preventDefault();

    let formData = new FormData();
    formData.append('name', this.state.name);
    formData.append('image', this.state.avatar);
    formData.append('preference1', this.state.preference1);
    formData.append('preference1', this.state.preference2);
    formData.append('preference1', this.state.preference3);


    axios.post('/api/v1/profile/create/', formData, {
      headers : {
        'content-type': 'multipart/form-data'
      }
    })
    .then(res => {
      let profile = [...this.state.profile];
      this.setState({profile: profile, name:'', preview: null, image: null, preference1:'', preference2:'', preference3:'' });
    })
    .catch(error =>{
      console.log(error);
    });
  }

    componentDidMount() {
      axios.get('api/v1/profile/detail/<int:pk>/')
    .then(res => {
      console.log(res);
      this.setState({profile: res.data});
    })
    .catch(error => {
      console.log(error);
    });
  }
  render(){
    console.log(this.state);
    return (
      <React.Fragment>
        <section className='aside-content'>
          <form onSubmit={this.handleSubmit}>
          <p>Create Your Profile</p>
          <p>Enter Your Name</p>
          <input type='text' name='name' value={this.state.name} onChange={this.handleChange}/>
          <p>Add a picture!</p>
          {this.state.avatar ? (
            <img src={this.state.preview} alt='preview'/>
          ):(
            <input type='file' name='avatar' onChange={this.handleAvatarChange}/>
          )}
          <p>Preference 1</p>
          <input type='checkbox' name='preference1' checked={this.state.preference1} onChange={this.handleChange} />
          <p>Preference 2</p>
          <input type='checkbox' name='preference2' checked={this.state.preference2} onChange={this.handleChange} />
          <p>Preference 3</p>
          <input type='checkbox' name='preference3' checked={this.state.preference3} onChange={this.handleChange} />
          <button>Save</button>
          </form>
        </section>
      </React.Fragment>
    )
  }
}

export default ProfileCreate;
